body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body, html {
  height: 100%;
  width: 100%;
  margin: 0;
}

#root {
  height: 100%;
}

.app {
  height: 100%;
  display: flex;
  flex-direction: column;
  /*max-width: 850px;*/
  margin: 0 auto;
}

.content-container {
  flex-grow: 1;
  display: flex;
}

.input-container {
  flex-grow: 1;
}

.input-container.interrupted input{
  background: #ff000061;
}

input {
  width: 100%;
  height: 100px;
  padding: 20px;
  font-size: 20px;
  border: none;
  box-sizing: border-box;
  outline: none;
  transition: background-color 1s;
}

.text-container, .measurements-container {
  padding: 20px;
}

.measurement {
  white-space: nowrap;
}

.measurement:before {
  content: 'ðŸ•‘';
}

.hg-layout-default .hg-button.hg-standardBtn {
  flex-grow: 1;
}

.hg-layout-default .hg-button.gap {
  background: none;
  box-shadow: none;
  border: none;
  flex-grow: 2;
}
